.form {
  @include page-grid;

  row-gap: 48px;

  &__value {
    font-family: 'Space Mono', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
    color: #191919;
    margin-top: 0;
    margin-bottom: 24px;
  }

  &__atribute {
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 400;
    color: #9393a3;
    margin-top: 0;
    margin-bottom: 8px;
  }



  &__elements {
    grid-column: 1 / -1;

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__content {
    margin-bottom: 24px;
  }

  &__contacts {
    grid-column: 1 / -1;

    @include on-desktop {
      grid-column: 7 / -1;
    }
  }

  &__input {
    font-family: 'Space Mono', sans-serif;
    width: 100%;
    height: 48px;
    margin: 0;
    padding-left: 16px;
    box-sizing: border-box;
    border: solid 1px #f8f8fa;
    border-radius: 8px;
    background-color: #f8f8fa;
    margin-bottom: 16px;

    &--button {
      font-family: 'Space Grotesk', sans-serif;
      font-size: 16px;
      line-height: 27px;
      font-weight: 700;
      color: #fff;
      background-color: #{$main-color};
      width: 100%;
      height: 48px;
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      border: 1px;
      border-radius: 8px;
      transition: all 0.3s;

      &:hover {
        background-color: #f8f8fa;
        border: solid 1px #f0f1f4;
        color: #{$main-color};
      }

      &:active {
        background-color: #111;
        border: solid 1px #111;
        color: #fff;
      }
    }

    &--big {
      height: 148px;
      padding: 14px 16px;
    }

    &:hover {
      box-sizing: border-box;
      border: solid 1px #d5d7de;
    }

    &::after {
      border: solid 1px #292929;
    }

    &__atributes {
      font-weight: 400;
      font-size: 14px;
      line-height: 19.6px;
      color: #9393a3;
    }
  }
}
