.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: left;

  @include on-tablet {
    font-size: 40px;
    margin-bottom: 56px;
  }

  @include on-desktop {
    font-size: 48px;
  }
}

.section-content {
  font-family: 'Space Mono', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22.4px;
}
